@media only screen and (max-width: 850px) {
   
    .navbar{
        height: 60px;
    }

    .main-nav{
        height: 60px;
        
    }
    .logo-container p {
        display: none;
    }
    .nav-links {
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: absolute;
        text-align: center;
        width: 100%;
        height: 100%;
        top: 60px;
        left: 100%;
        transition: 0.5s all;
    }
    .nav-links.active {
        background: var(--secondary);
        height: calc(100vh - 60px);
        left: 0;
    }
    .nav-item {
