.close-button{
        border: 0;
        color: white;
        font-family: system-ui, sans-serif;
        text-decoration: none;      
}

.close {
    position: absolute;
    top: 10px;
    right: 5px;
    width: 32px;
    height: 32px;
    opacity: 0.5;
  }

  .close-button:hover .close {
    opacity: 1;
  }


  .close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 20px;
    width: 1px;
    background-color: #333;
  }

  .close:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
  }

  .close:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
  }
  


/* added to the body element when shown; prevent scrolling */
.react-router-modal__modal-open {
  overflow: hidden;
}

/* container that contains all modals and backdrops */
.react-router-modal__container {
  position: fixed;
  z-index: 1000
}

/* wrapper that contains backdrop and modal */
/* setting this to absolute and setting a z-index creates a new stacking context */
.react-router-modal__wrapper {
  position: absolute;
  z-index: 1;
}

/* backdrop shown behind each modal */
.react-router-modal__backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background-color: rgba(0,0,0,0.5)
}


/* modal element for the modal component */
.react-router-modal__modal {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

/* mobile devices, particularly iOS, have some quirks to work around */
@media only screen and (max-device-width: 1024px) {
  .react-router-modal__modal-open {
    position: fixed;
    overflow: hidden;
    -webkit-overflow-scrolling: touch;
  }
  .react-router-modal__container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0)
  }
}

.image-viewer {
    display: flex;
    justify-content: flex-end;
    width: 50%;
    height: 100%;
}

.thumbnails {
    display: flex;
    flex-direction: column;
}

.thumbnails img {
    width: 100px;
    height: 100px;
    margin: 10px;
    object-fit: cover;
    cursor: pointer;
    transition: all 0.5s ease;
}

.thumbnails img:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.main-image {
    margin: 10px;
    flex-basis: 70%;
    width: 10%;
    display: block;
    object-fit: cover;
}

@media only screen and (max-width: 480px) {
    .image-viewer {
